import React, { useState, useEffect } from "react";
import DefaultLayout from "../../components/layouts/Default/Default";
import { Grid, Header, Card } from "semantic-ui-react";
import "./OnboardingProfile.scss";
import { getAllApps, updateUserProfile } from "../../store/actions/index";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import appRoutes from "../../configs/routes";
import ProfileForm from "../../components/forms/ProfileForm/ProfileForm";
import switchPage from "../../configs/onboardingHandler";

const OnboardingProfilePage = (props) => {
  const updateUserProfile = (data) => {
    props.updateUserProfile(data);
  };

  return (
    <DefaultLayout>
      <Grid container columns="3">
        <Grid.Row stretched centered>
          <Grid.Column mobile={16} tablet={16} computer={6}>
            <Grid columns="5">
              <Grid.Row
                style={{
                  minHeight: 400,
                }}
              >
                <Grid.Column
                  mobile={16}
                  tablet={16}
                  computer={16}
                  style={{
                    padding: "2rem 0",
                  }}
                  className="OnboardingProfile"
                >
                  <Card fluid>
                    <Card.Content className="OnboardingProfile-card">
                      <Header as="h1" className="OnboardingProfile-header">
                        Update Profile
                      </Header>
                      <ProfileForm
                        submitted={updateUserProfile}
                        isLoading={props.isLoading}
                      />
                    </Card.Content>
                  </Card>
                </Grid.Column>
              </Grid.Row>
            </Grid>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    </DefaultLayout>
  );
};

const mapStateToProps = (state) => {
  return {
    user: state.user,
    isAuthenticated: state.isAuthenticated,
    isLoading: state.isLoading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    getAllApps: () => dispatch(getAllApps()),
    updateUserProfile: (data) =>
      dispatch(
        updateUserProfile({
          ...data,
          onboardingPhase: 2,
        })
      ),
  };
};

export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(OnboardingProfilePage)
);
