import React, { useState, useEffect } from "react";
import DefaultLayout from "../../components/layouts/Default/Default";
import { withRouter } from "react-router-dom";
import { Header, Grid, Button, Image, Divider } from "semantic-ui-react";
import chromeExtensionIcon from "../../assets/icons/chrome-extension-icon.svg";
import { Alert, message, Popconfirm } from "antd";

import VerifyChromeExtension from "../../components/utils/VerifyChromeExtension/VerifyChromeExtension";
import DownloadChromeExtension from "../../components/utils/DownloadChromeExtension/DownloadChromeExtension";
import { updateUserProfile } from "../../store/actions/index";
import { connect } from "react-redux";
import appRoutes from "../../configs/routes";
import "./ChromeExtension.scss";
import switchPage from "../../configs/onboardingHandler";

const ChromeExtensionPage = (props) => {
  const [isDownloadButtonClicked, setisDownloadButtonClicked] = useState(false);

  useEffect(() => {
    // const element = document.getElementById(
    //   "dashdashgo-chrome-extension-verification"
    // );
    // if (element) {
    //   props.updateUserProfile();
    // }
    if (localStorage.getItem("downloadChromeExtension"))
      setisDownloadButtonClicked(true);
  }, []);

  const downloadChromeExtension = () => {
    setisDownloadButtonClicked(true);
  };

  const verifyChromeExtension = (status) => {
    if (status) {
      props.updateUserProfile();
    } else {
      message.warning(
        "Chrome extension isn't installed. Please install as it is mandatary."
      );
    }
  };

  const skipDownload = () => {
    props.updateUserProfile();
  };

  return (
    <DefaultLayout contentClass="chromeExtension-bg">
      <Grid container columns="3">
        <Grid.Row centered>
          <Grid.Column
            mobile={16}
            tablet={16}
            computer={10}
            className="chromeExtension"
          >
            <Alert
              message="Important!"
              description="DashDashGo chrome extension is mandatory for the application to work."
              banner
              className="chromeExtension-annonucement"
            />
            <Image src={chromeExtensionIcon} size="tiny"></Image>
            <Header as="div" className="chromeExtension-header">
              Get more done with the our Chrome Extension
            </Header>
            <div className="chromeExtension-subtitle">
              Now more simple, secure, and faster than ever - with ours smarts
              built-in.
            </div>
            {!isDownloadButtonClicked ? (
              <DownloadChromeExtension
                isLoading={props.isLoading}
                downloaded={downloadChromeExtension}
                style={{
                  marginBottom: "2.5rem",
                }}
              />
            ) : (
              <VerifyChromeExtension
                isLoading={props.isLoading}
                verification={verifyChromeExtension}
                style={{
                  marginBottom: "2.5rem",
                }}
              />
            )}
            <Divider horizontal>Or</Divider>
            <Button
              basic
              color="primary"
              size="large"
              circular
              onClick={skipDownload}
              style={{
                marginTop: "2.5rem",
              }}
              loading={props.isLoading}
            >
              Skip if already done?
            </Button>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    </DefaultLayout>
  );
};

const mapStateToProps = (state) => {
  return {
    userApps: state.userApps,
    user: state.user,
    isLoading: state.isLoading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    updateUserProfile: () =>
      dispatch(
        updateUserProfile({
          onboardingPhase: 4,
        })
      ),
  };
};

export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(ChromeExtensionPage)
);
