import React, { useState, useEffect } from "react";
import DefaultLayout from "../../components/layouts/Default/Default";
import { Grid, Image, Header, Button, Loader } from "semantic-ui-react";
import "./Onboarding.scss";
import AppCard from "../../components/cards/AppCard/AppCard";
import userImageIcon from "../../assets/icons/user_image.svg";
import { getAllApps, updateUserProfile } from "../../store/actions/index";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import appRoutes from "../../configs/routes";

const OnboardingPage = (props) => {
  const [selectedApps, setselectedApps] = useState([]);

  useEffect(() => {
    props.getAllApps();
    if (props.isAuthenticated && props.user && props.user.onboardingPhase < 2)
      props.history.push(appRoutes.loginPage);
  }, []);

  // useEffect(() => {
  //   const element = document.getElementById(
  //     "dashdashgo-chrome-extension-verification"
  //   );
  //   if (element) {
  //     localStorage.setItem("downloadChromeExtension", true);
  //   }
  // }, []);

  const onAppsSelects = (isSelected, obj) => {
    if (!isSelected)
      setselectedApps((prev) => {
        return prev.filter((ele) => ele.app !== obj.app);
      });
    else setselectedApps((prev) => prev.concat(obj));
  };

  const onSubmit = () => {
    props.addUserApps(selectedApps);
  };

  return (
    <DefaultLayout>
      <Grid container columns="3" className="login">
        <Grid.Row centered className="Onboarding">
          <Grid.Column
            mobile={16}
            tablet={16}
            computer={10}
            className="Onboarding-header"
          >
            <Header
              as="h2"
              icon={<Image src={userImageIcon} />}
              content={`Hi ${props.user && props.user.name},`}
              subheader="Select the apps that power your business"
            />
            <Button
              floated
              color="green"
              size="large"
              disabled={!selectedApps.length}
              onClick={onSubmit}
              loading={props.isLoading}
            >
              Select Apps
            </Button>
          </Grid.Column>
        </Grid.Row>
        <Grid.Row stretched centered>
          <Grid.Column mobile={16} tablet={16} computer={10}>
            <Grid columns="5">
              <Grid.Row
                style={{
                  minHeight: 400,
                }}
              >
                <Loader size="large" active={!props.allApps.length}>
                  Loading
                </Loader>
                {props.allApps.map((obj) =>
                  obj.header ? (
                    <Grid.Column
                      mobile={16}
                      tablet={16}
                      computer={16}
                      style={{
                        margin: "1rem 0",
                      }}
                    >
                      <div className="Onboarding-title">{obj.header}</div>
                    </Grid.Column>
                  ) : (
                    <Grid.Column
                      mobile={6}
                      tablet={4}
                      computer={4}
                      style={{
                        margin: "2rem 0",
                      }}
                    >
                      <AppCard
                        data={obj}
                        onClicked={onAppsSelects}
                        clickable={true}
                      ></AppCard>
                    </Grid.Column>
                  )
                )}
                <Grid.Column
                  mobile={16}
                  tablet={16}
                  computer={16}
                  style={{
                    padding: "2rem 0",
                  }}
                >
                  <Button
                    fluid
                    color="green"
                    size="large"
                    disabled={!selectedApps.length}
                    onClick={onSubmit}
                    loading={props.isLoading}
                  >
                    Select Apps
                  </Button>
                </Grid.Column>
              </Grid.Row>
            </Grid>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    </DefaultLayout>
  );
};

const mapStateToProps = (state) => {
  return {
    userApps: state.userApps,
    user: state.user,
    isAuthenticated: state.isAuthenticated,
    allApps: state.allApps,
    isLoading: state.isLoading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    getAllApps: () => dispatch(getAllApps()),
    addUserApps: (payload) =>
      dispatch(
        updateUserProfile(
          {
            userApps: payload,
            onboardingPhase: 3,
          },
          true
        )
      ),
  };
};

export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(OnboardingPage)
);
