import axios from "axios";
import { message } from "antd";
import environments from "./configs/environments/environments";

const axiosInstance = axios.create({
  baseURL: environments.serverHost,
});

const isHandlerEnabled = (config = {}) => {
  return config.hasOwnProperty("handlerEnabled") && !config.handlerEnabled
    ? false
    : true;
};

const requestHandler = (request) => {
  if (isHandlerEnabled(request)) {
    // Modify request here
    request.headers["X-auth-token"] = localStorage.getItem("X-auth-token", "");
  }
  return request;
};

axiosInstance.interceptors.request.use((request) => requestHandler(request));

const errorHandler = (error) => {
  message.error(
    `${error.response.statusText}: ${error.response.data.errors[0].msg}`
  );
  if (isHandlerEnabled(error.config)) {
    // Handle errors
  }
  return Promise.reject({ ...error });
};

const successHandler = (response) => {
  if (isHandlerEnabled(response.config)) {
    // Handle responses
  }
  return response;
};

axiosInstance.interceptors.response.use(
  (response) => successHandler(response),
  (error) => errorHandler(error)
);

export default axiosInstance;
