import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import appRoutes from "../../../configs/routes";
import { changeUserPassword } from "../../../store/actions";
import { Form, Grid, Dropdown, Header } from "semantic-ui-react";
import "./UpdatePasswordForm.scss";

const UpdatePasswordForm = React.memo((props) => {
  const [newPasswordErr, setnewPasswordErr] = useState(null);
  const [currentPasswordErr, setcurrentPasswordErr] = useState(null);
  const [confirmPasswordErr, setconfirmPasswordErr] = useState(null);
  const [isError, setisError] = useState(true);
  const [userProfile, setuserProfile] = useState({
    currentPassword: "",
    newPassword: "",
    confirmPassword: "",
  });

  useEffect(() => {
    validateNewPassword();
    validateCurrentPassword();
    validateConfirmPassword();
  }, [userProfile]);

  const validateCurrentPassword = (show = false) => {
    setcurrentPasswordErr(false);
    setisError(false);

    if (!userProfile.currentPassword) {
      show &&
        setcurrentPasswordErr({
          content: "Enter your Current Password",
          pointing: "below",
        });
      setisError(true);
    }
  };

  const validateNewPassword = (show = false) => {
    setnewPasswordErr(false);
    setisError(false);

    if (!userProfile.newPassword) {
      show &&
        setnewPasswordErr({
          content: "Enter your New Password",
          pointing: "below",
        });
      setisError(true);
    } else if (userProfile.newPassword.length < 8) {
      setcurrentPasswordErr({
        content: "Password must be greater than length 7",
        pointing: "below",
      });
      setisError(true);
    }
  };

  const validateConfirmPassword = (show = false) => {
    setconfirmPasswordErr(null);
    setisError(false);

    if (!userProfile.confirmPassword) {
      show &&
        setconfirmPasswordErr({
          content: "Enter your Confirm password",
          pointing: "below",
        });
      setisError(true);
    } else if (userProfile.confirmPassword.length < 8) {
      setconfirmPasswordErr({
        content: "Password must be greater than length 7",
        pointing: "below",
      });
      setisError(true);
    }
  };

  const validateForm = (e) => {
    e.preventDefault();

    validateCurrentPassword(true);
    validateNewPassword(true);
    validateConfirmPassword(true);

    if (userProfile.newPassword !== userProfile.confirmPassword) {
      setisError(true);
      setcurrentPasswordErr({
        content: "Password Mismatch",
        pointing: "below",
      });
      setconfirmPasswordErr({
        content: "Password Mismatch",
        pointing: "below",
      });
    }

    if (!isError) {
      props.changeUserPassword({
        currentPassword: userProfile.currentPassword,
        newPassword: userProfile.newPassword,
      });
      setTimeout(() => {
        setuserProfile({
          currentPassword: "",
          newPassword: "",
          confirmPassword: "",
        });
      }, 1500);
    }
  };

  return (
    <Form onSubmit={validateForm}>
      <Form.Field
        style={{
          margin: "2rem 0",
        }}
      >
        <Form.Input
          id="form-input-control-error-currentPassword"
          type="password"
          fluid
          icon="eye slash outline"
          iconPosition="left"
          placeholder="Current Password"
          value={userProfile.currentPassword}
          error={currentPasswordErr}
          onChange={(event) => {
            const _currentPassword = event.target.value;
            setuserProfile((prev) => ({
              ...prev,
              currentPassword: _currentPassword,
            }));
            validateCurrentPassword();
          }}
        />
      </Form.Field>
      <Form.Field
        style={{
          margin: "2rem 0",
        }}
      >
        <Form.Input
          id="form-input-control-error-newPassword"
          type="password"
          fluid
          icon="lock"
          iconPosition="left"
          placeholder="New Password"
          value={userProfile.newPassword}
          error={newPasswordErr}
          onChange={(event) => {
            const _newPassword = event.target.value;
            setuserProfile((prev) => ({
              ...prev,
              newPassword: _newPassword,
            }));
            validateNewPassword();
          }}
        />
      </Form.Field>
      <Form.Field
        style={{
          margin: "2rem 0",
        }}
      >
        <Form.Input
          id="form-input-control-error-confirmPassword"
          type="password"
          fluid
          icon="lock"
          iconPosition="left"
          placeholder="Confirm Password"
          value={userProfile.confirmPassword}
          error={confirmPasswordErr}
          onChange={(event) => {
            const _confirmPassword = event.target.value;
            setuserProfile((prev) => ({
              ...prev,
              confirmPassword: _confirmPassword,
            }));
            validateConfirmPassword();
          }}
        />
      </Form.Field>
      <Form.Field>
        <Form.Button
          id="form-button-control-public"
          iconPosition="left"
          classCurrentPassword="profile-button padded"
          color="green"
          onClick={true}
          loading={props.isLoading}
          disabled={isError}
        >
          Update Password
        </Form.Button>
      </Form.Field>
    </Form>
  );
});

const mapStateToProps = (state) => {
  return {
    userApps: state.userApps,
    isLoading: state.isLoading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    changeUserPassword: (userProfile) =>
      dispatch(changeUserPassword(userProfile)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(UpdatePasswordForm);
