import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import appRoutes from "../../../configs/routes";
import { addNewApp } from "../../../store/actions";
import { Form, Grid, Dropdown, Header, Radio } from "semantic-ui-react";

const AddAppForm = React.memo((props) => {
  const [urlErr, seturlErr] = useState(null);
  const [nameErr, setnameErr] = useState(null);
  const [categoryErr, setcategoryErr] = useState(null);
  const [isError, setisError] = useState(true);
  const [isFaviconError, setisFaviconError] = useState(true);
  const [appDetails, setappDetails] = useState({
    name: "",
    url: "",
    icon: "",
    exact: true,
    category: "",
    // isNewCategory: false,
  });

  const handleAdditionCategory = (e, { value }) => {
    console.log(e, value);
    setcategoryErr(false);
    setappDetails((prev) => {
      return {
        ...prev,
        category: value,
      };
    });
  };

  const handleChangeCategory = (e, { value }) => {
    setcategoryErr(false);
    setappDetails((prev) => {
      return {
        ...prev,
        category: value,
      };
    });
  };

  useEffect(() => {
    validateName();
    validateUrl();
    validateCategory();
  }, [appDetails]);

  useEffect(() => {
    setappDetails((prev) => {
      const regex = /(http[s]{0,1}:\/\/)(?:[a-z0-9](?:[a-z0-9-]{0,61}[a-z0-9])?\.)+[a-z0-9][a-z0-9-]{0,61}/;
      const match = regex.exec(appDetails.url);
      if (appDetails.url && match) {
        console.log(match, match[0]);
        setisFaviconError(false);
        return { ...prev, icon: `${match[0]}/favicon.ico` };
      } else {
        return prev;
      }
    });
  }, [appDetails.url]);

  const iconLoaded = (e) => {
    setisFaviconError(false);
    console.log(e);
  };

  const iconError = (e) => {
    setisFaviconError(true);
    console.log(e);
  };

  const validateUrl = (show = false) => {
    seturlErr(false);
    setisError(false);

    const domainRegex = /^(http[s]{0,1}:\/\/)(?:[a-z0-9](?:[a-z0-9-]{0,61}[a-z0-9])?\.)+[a-z0-9][a-z0-9-]{0,61}[a-z0-9\/A-Z]$/;

    if (!appDetails.url) {
      show &&
        seturlErr({
          content: "Enter App Url",
          pointing: "below",
        });
      setisError(true);
    } else if (!domainRegex.test(appDetails.url)) {
      show &&
        seturlErr({
          content: "Invalid Host Name",
          pointing: "below",
        });
      setisError(true);
    }
  };

  const getValidation = () => {
    console.log(appDetails.category && appDetails.name && appDetails.url);
    if (appDetails.category && appDetails.name && appDetails.url) return false;
    return true;
  };

  const validateName = (show = false) => {
    setnameErr(false);
    setisError(false);

    if (!appDetails.name) {
      show &&
        setnameErr({
          content: "Enter App Name",
          pointing: "below",
        });
      setisError(true);
    }
  };

  const validateCategory = (show = false) => {
    setcategoryErr(false);
    setisError(false);

    if (!appDetails.category) {
      show &&
        setnameErr({
          content: "Enter App Name",
          pointing: "below",
        });
      setisError(true);
    }
  };

  const validateForm = (e) => {
    e.preventDefault();

    validateUrl(true);
    validateName(true);

    if (!isError) {
      setTimeout(() => {
        props.close();
      }, 1500);
      props.addNewApp(appDetails);
    }
  };

  return (
    <Grid container columns="3">
      <Grid.Row stretched centered>
        <Grid.Column
          mobile={16}
          tablet={8}
          computer={6}
          style={{
            padding: "0 2rem",
          }}
        >
          <div
            style={{
              display: "flex",
            }}
          >
            {isFaviconError ? (
              <div
                style={{
                  width: "100%",
                  height: "auto",
                  alignSelf: "center",
                }}
              >
                No Favicon Found
              </div>
            ) : (
              <img
                src={appDetails.icon}
                onLoad={iconLoaded}
                onError={iconError}
                style={{
                  width: "100%",
                  height: "auto",
                  alignSelf: "center",
                }}
                alt="APP ICON"
              />
            )}
          </div>
        </Grid.Column>
        <Grid.Column mobile={16} tablet={8} computer={10}>
          <Header as="h2">Add Application</Header>
          <Form onSubmit={validateForm}>
            <Form.Field
              style={{
                margin: "2rem 0",
              }}
            >
              <Form.Input
                id="form-input-control-error-name"
                fluid
                icon="universal access"
                iconPosition="left"
                placeholder="Your App Name"
                value={appDetails.name}
                error={nameErr}
                onChange={(event) => {
                  const _name = event.target.value;
                  setappDetails((prev) => ({
                    ...prev,
                    name: _name,
                  }));
                  validateName();
                }}
              />
            </Form.Field>
            <Form.Field
              style={{
                margin: "2rem 0",
              }}
            >
              <Form.Input
                id="form-input-control-error-url"
                fluid
                icon="globe"
                iconPosition="left"
                placeholder="Your App URL Address"
                value={appDetails.url}
                error={urlErr}
                onChange={(event) => {
                  const _url = event.target.value;
                  setappDetails((prev) => ({
                    ...prev,
                    url: _url,
                  }));
                  validateUrl();
                }}
              />
              <span
                style={{
                  color: "gray",
                  fontSize: "0.9rem",
                }}
              >
                Eg: https://www.example.com
              </span>
            </Form.Field>
            <Form.Field
              style={{
                margin: "2rem 0",
              }}
            >
              <Dropdown
                options={props.categories}
                placeholder="Choose Category"
                search
                selection
                fluid
                allowAdditions
                searchInput="name"
                additionLabel="Add Custom: "
                value={appDetails.category}
                onAddItem={handleAdditionCategory}
                onChange={handleChangeCategory}
              ></Dropdown>
            </Form.Field>
            <Form.Field
              style={{
                margin: "2rem 0",
              }}
            >
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "center",
                  paddingRight: "1rem",
                }}
              >
                <div>Do you want to include sub-domains?</div>
                <Radio
                  slider
                  value={appDetails.exact}
                  checked={appDetails.exact}
                  onChange={() => {
                    setappDetails((prev) => {
                      return {
                        ...prev,
                        exact: !prev.exact,
                      };
                    });
                  }}
                />
              </div>
            </Form.Field>
            <Form.Field>
              <Form.Button
                id="form-button-control-public"
                fluid
                iconPosition="left"
                color="blue"
                onClick={validateForm}
                loading={props.isLoading}
                disabled={isError}
              >
                ADD APP
              </Form.Button>
            </Form.Field>
          </Form>
        </Grid.Column>
      </Grid.Row>
    </Grid>
  );
});

const mapStateToProps = (state) => {
  return {
    userApps: state.userApps,
    allApps: state.allApps,
    categories: state.categories.map((obj) => {
      return {
        text: obj.name,
        key: obj._id,
        value: obj.name,
      };
    }),
    isLoading: state.isLoading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    addNewApp: (appDetails) => dispatch(addNewApp(appDetails)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(AddAppForm);
