import React from "react";
import { Button } from "semantic-ui-react";
import environments from "../../../configs/environments/environments";

const DownloadChromeExtension = (props) => {
  const downloadChromeExtension = () => {
    localStorage.setItem("downloadChromeExtension", true);
    props.downloaded();
  };
  return (
    <React.Fragment>
      <Button
        as="a"
        color="primary"
        size="large"
        circular
        style={{ ...props.style }}
        onClick={downloadChromeExtension}
        href={environments.chromeExtensionLink}
        target="_blank"
        isLoading={props.isLoading}
      >
        Download Chrome Extension
      </Button>
    </React.Fragment>
  );
};

export default DownloadChromeExtension;
