import React, { useState, useEffect } from "react";
import { Form, Header } from "semantic-ui-react";
import "./EditAppUrlForm.scss";
import { connect } from "react-redux";
import { updateAppUrl } from "../../../store/actions";

const EditAppUrlForm = React.memo((props) => {
  const [urlErr, seturlErr] = useState(null);
  const [isError, setisError] = useState(false);
  const [appDetails, setappDetails] = useState({
    _id: "",
    url: "",
  });

  useEffect(() => {
    setappDetails({ app: props.app.appId, url: props.app.url });
  }, [props.app.appId]);

  useEffect(() => {
    validateUrl();
  }, [appDetails]);

  const validateUrl = (show = false) => {
    seturlErr(false);
    setisError(false);

    const domainRegex = /^(http[s]{0,1}:\/\/)(?:[a-z0-9](?:[a-z0-9-]{0,61}[a-z0-9])?\.)+[a-z0-9][a-z0-9-]{0,61}[a-z0-9\/A-Z]$/;

    if (!appDetails.url) {
      show &&
        seturlErr({
          content: "Enter your App URL",
          pointing: "below",
        });
      setisError(true);
    } else if (!domainRegex.test(appDetails.url)) {
      show &&
        seturlErr({
          content: "Invalid URL",
          pointing: "below",
        });
      setisError(true);
    }
  };

  const validateForm = (e) => {
    e.preventDefault();

    validateUrl(true);

    if (!isError && appDetails.app) {
      setTimeout(() => {
        props.submitted(true);
      }, 1500);
      props.updateAppUrl(appDetails);
    }
  };

  return (
    <Form onSubmit={validateForm}>
      <Header as="div">Edit {props.app.name} URL</Header>
      <Form.Field
        style={{
          margin: "2rem 0",
        }}
      >
        <Form.Input
          id="form-input-control-error-url"
          fluid
          icon="globe"
          iconPosition="left"
          placeholder="Your App URL Address"
          value={appDetails.url}
          error={urlErr}
          onChange={(event) => {
            const _url = event.target.value;
            setappDetails((prev) => ({
              ...prev,
              url: _url,
            }));
            validateUrl();
          }}
        />
        <span
          style={{
            color: "gray",
            fontSize: "0.9rem",
          }}
        >
          Eg: https://www.example.com
        </span>
      </Form.Field>
      <Form.Field>
        <Form.Button
          id="form-button-control-public"
          fluid
          iconPosition="left"
          color="blue"
          onClick={validateForm}
          loading={props.isLoading}
          disabled={isError}
        >
          Update URL
        </Form.Button>
      </Form.Field>
    </Form>
  );
});

const mapStateToProps = (state) => {
  return {
    userApps: state.userApps,
    isLoading: state.isLoading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    updateAppUrl: (appDetails) => dispatch(updateAppUrl(appDetails)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(EditAppUrlForm);
