import { actionTypes } from "../actions";

const initialState = {
  user: null,
  frequentApps: [],
  userApps: [],
  allApps: [],
  categories: [],
  isAuthenticated: false,
  err: null,
  isLoading: false,
};

// Auth User
const authLogin = (state, action) => {
  return {
    ...state,
    err: null,
    isLoading: true,
  };
};
const authLoginSuccess = (state, action) => {
  return {
    ...state,
    user: action.payload,
    isAuthenticated: true,
    err: null,
    isLoading: false,
  };
};
const authLoginFailure = (state, action) => {
  return {
    ...state,
    err: action.error,
    isAuthenticated: false,
    isLoading: false,
  };
};

const retryLogin = (state, action) => {
  return {
    ...state,
    err: null,
    isLoading: true,
  };
};
const retryLoginSuccess = (state, action) => {
  return {
    ...state,
    isAuthenticated: true,
    err: null,
    isLoading: false,
  };
};
const retryLoginFailure = (state, action) => {
  return {
    ...state,
    err: action.error,
    isAuthenticated: false,
    isLoading: false,
  };
};

const logout = (state, action) => {
  return {
    ...state,
    err: null,
    isLoading: true,
  };
};
const logoutSuccess = (state, action) => {
  return {
    ...state,
    user: null,
    frequentApps: [],
    userApps: [],
    allApps: [],
    isAuthenticated: false,
    err: null,
    isLoading: false,
  };
};
const logoutFailure = (state, action) => {
  return {
    ...state,
    err: action.payload,
    isLoading: false,
  };
};

