//  NEW
export const authRegister = (state, action) => {
  return {
    ...state,
    err: null,
    isLoading: true,
  };
};

export const authRegisterSuccess = (state, action) => {
  localStorage.setItem("X-auth-token", action.payload.token);
  return {
    ...state,
    user: action.payload,
    isAuthenticated: true,
    err: null,
    isLoading: false,
  };
};

export const authRegisterFailure = (state, action) => {
  return {
    ...state,
    err: action.err,
    isLoading: false,
  };
};

