export const updateAppUrl = (state, action) => {
  return {
    ...state,
    err: null,
    isLoading: true,
  };
};

export const updateAppUrlSuccess = (state, action) => {
  return {
    ...state,
    userApps: action.payload.userApps.map((obj) => {
      return {
        _id: obj._id,
        appId: obj.app._id,
        name: obj.app.name,
        url: obj.url,
        icon: obj.app.icon,
      };
    }),
    frequentApps: action.payload.frequentApps.map((obj) => {
      return {
        _id: obj._id,
        appId: obj.app._id,
        name: obj.app.name,
        url: obj.url,
        icon: obj.app.icon,
        frequency: obj.frequency,
      };
    }),
    err: null,
    isLoading: false,
  };
};

export const updateAppUrlFailure = (state, action) => {
  return {
    ...state,
    err: action.err,
    isLoading: false,
  };
};

export const updateUserProfile = (state, action) => {
  return {
    ...state,
    err: null,
    isLoading: true,
  };
};

export const updateUserProfileSuccess = (state, action) => {
  return {
    ...state,
    user: {
      ...state.user,
      ...action.payload,
    },
    userApps: action.payload.userApps.map((obj) => {
      return {
        _id: obj._id,
        appId: obj.app._id,
        name: obj.app.name,
        url: obj.url,
        icon: obj.app.icon,
      };
    }),
    frequentApps: action.payload.frequentApps.map((obj) => {
      return {
        _id: obj._id,
        appId: obj.app._id,
        name: obj.app.name,
        url: obj.url,
        icon: obj.app.icon,
        frequency: obj.frequency,
      };
    }),
    isOnboarded: true,
    err: null,
    isLoading: false,
  };
};

export const updateUserProfileFailure = (state, action) => {
  return {
    ...state,
    isOnboarded: false,
    err: action.err,
    isLoading: false,
  };
};

