const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.RETRY_LOGIN:
      return retryLogin(state, action);
    case actionTypes.RETRY_LOGIN_SUCCESS:
      return retryLoginSuccess(state, action);
    case actionTypes.RETRY_LOGIN_FAILURE:
      return retryLoginFailure(state, action);
    case actionTypes.LOGOUT:
      return logout(state, action);
    case actionTypes.LOGOUT_SUCCESS:
      return logoutSuccess(state, action);
    case actionTypes.LOGOUT_FAILURE:
      return logoutFailure(state, action);
    case actionTypes.AUTH_LOGIN:
      return authLogin(state, action);
    case actionTypes.AUTH_LOGIN_SUCCESS:
      return authLoginSuccess(state, action);
    case actionTypes.AUTH_LOGIN_FAILURE:
      return authLoginFailure(state, action);
    case actionTypes.AUTH_REGISTER:
      return authRegister(state, action);
    case actionTypes.AUTH_REGISTER_SUCCESS:
      return authRegisterSuccess(state, action);
    case actionTypes.AUTH_REGISTER_FAILURE:
      return authRegisterFailure(state, action);
    case actionTypes.AUTH_RESET:
      return authReset(state, action);
    case actionTypes.AUTH_RESET_SUCCESS:
      return authResetSuccess(state, action);
    case actionTypes.AUTH_RESET_FAILURE:
      return authResetFailure(state, action);
    case actionTypes.AUTH_CHANGE_PASSWORD:
      return changeUserPassword(state, action);
    case actionTypes.AUTH_CHANGE_PASSWORD_SUCCESS:
      return changeUserPasswordSuccess(state, action);
    case actionTypes.AUTH_CHANGE_PASSWORD_FAILURE:
      return changeUserPasswordFailure(state, action);
    case actionTypes.ADD_NEW_APP:
      return addNewApp(state, action);
    case actionTypes.ADD_NEW_APP_SUCCESS:
      return addNewAppSuccess(state, action);
    case actionTypes.ADD_NEW_APP_FAILURE:
      return addNewAppFailure(state, action);
    case actionTypes.UPDATE_APP_URL:
      return updateAppUrl(state, action);
    case actionTypes.UPDATE_APP_URL_SUCCESS:
      return updateAppUrlSuccess(state, action);
    case actionTypes.UPDATE_APP_URL_FAILURE:
      return updateAppUrlFailure(state, action);
    case actionTypes.UPDATE_USER_DETAILS:
      return updateUserProfile(state, action);
    case actionTypes.UPDATE_USER_DETAILS_SUCCESS:
      return updateUserProfileSuccess(state, action);
    case actionTypes.UPDATE_USER_DETAILS_FAILURE:
      return updateUserProfileFailure(state, action);
    case actionTypes.INVITE_USERS:
      return invitesUsers(state, action);
    case actionTypes.INVITE_USERS_SUCCESS:
      return invitesUsersSuccess(state, action);
    case actionTypes.INVITE_USERS_FAILURE:
      return invitesUsersFailure(state, action);
    case actionTypes.GET_USER_DETAILS:
      return getUserDetails(state, action);
    case actionTypes.GET_USER_DETAILS_SUCCESS:
      return getUserDetailsSuccess(state, action);
    case actionTypes.GET_USER_DETAILS_FAILURE:
      return getUserDetailsFailure(state, action);
    case actionTypes.ADD_USER_APPS:
      return addUserApps(state, action);
    case actionTypes.ADD_USER_APPS_SUCCESS:
      return addUserAppsSuccess(state, action);
    case actionTypes.ADD_USER_APPS_FAILURE:
      return addUserAppsFailure(state, action);
    case actionTypes.UPDATE_USER_APPS:
      return updateUserApps(state, action);
    case actionTypes.UPDATE_USER_APPS_SUCCESS:
      return updateUserAppsSuccess(state, action);
    case actionTypes.UPDATE_USER_APPS_FAILURE:
      return updateUserAppsFailure(state, action);
    case actionTypes.UPDATE_FREQUENT_APPS:
      return updateFrequentApps(state, action);
    case actionTypes.UPDATE_FREQUENT_APPS_SUCCESS:
      return updateFrequentAppsSuccess(state, action);
    case actionTypes.UPDATE_FREQUENT_APPS_FAILURE:
      return updateFrequentAppsFailure(state, action);
    case actionTypes.REMOVE_USER_APPS:
      return removeUserApps(state, action);
    case actionTypes.REMOVE_USER_APPS_SUCCESS:
      return removeUserAppsSuccess(state, action);
    case actionTypes.REMOVE_USER_APPS_FAILURE:
      return removeUserAppsFailure(state, action);
    case actionTypes.GET_ALL_APP_CATEGORIES:
      return getAllAppCategories(state, action);
    case actionTypes.GET_ALL_APP_CATEGORIES_SUCCESS:
      return getAllAppCategoriesSuccess(state, action);
    case actionTypes.GET_ALL_APP_CATEGORIES_FAILURE:
      return getAllAppCategoriesFailure(state, action);
    case actionTypes.GET_ALL_APPS:
      return getAllApps(state, action);
    case actionTypes.GET_ALL_APPS_SUCCESS:
      return getAllAppsSuccess(state, action);
    case actionTypes.GET_ALL_APPS_FAILURE:
      return getAllAppsFailure(state, action);
    case actionTypes.GET_ALL_USER_APPS:
      return getAllUserApps(state, action);
    case actionTypes.GET_ALL_USER_APPS_SUCCESS:
      return getAllUserAppsSuccess(state, action);
    case actionTypes.GET_ALL_USER_APPS_FAILURE:
      return getAllUserAppsFailure(state, action);
    case actionTypes.GET_ALL_FREQUENT_APPS:
      return getAllFrequentApps(state, action);
    case actionTypes.GET_ALL_FREQUENT_APPS_SUCCESS:
      return getAllFrequentAppsSuccess(state, action);
    case actionTypes.GET_ALL_FREQUENT_APPS_FAILURE:
      return getAllFrequentAppsFailure(state, action);
    default:
      return state;
  }
};

export default reducer;
