import React, { useState, useEffect } from "react";
import { Form, Input } from "semantic-ui-react";
import "./LoginForm.scss";
import { authLogin } from "../../../store/actions";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import appRoutes from "../../../configs/routes";

const LoginForm = React.memo((props) => {
  const [emailErr, setEmailErr] = useState({
    content: "",
    pointing: "below",
  });
  const [passErr, setPassErr] = useState({
    content: "",
    pointing: "below",
  });
  const [loginCreds, setLoginCreds] = useState({
    email: "",
    password: "",
  });
  const [isError, setisError] = useState(false);

  useEffect(() => {
    resetErrs();
  }, []);

  const resetErrs = () => {
    setEmailErr(null);
    setPassErr(null);
  };

  const validateEmail = () => {
    setEmailErr(null);
    setisError(false);
    const emailRegex = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;

    if (!loginCreds.email) {
      setEmailErr({
        content: "Enter your email address",
        pointing: "below",
      });
      setisError(true);
    } else if (!emailRegex.test(loginCreds.email)) {
      setEmailErr({
        content: "Invalid email address",
        pointing: "below",
      });
      setisError(true);
    }
  };

  const validatePass = () => {
    setPassErr(null);
    setisError(false);
    if (!loginCreds.password) {
      setPassErr({
        content: "Enter your password",
        pointing: "below",
      });
      setisError(true);
    }
  };

  const validateForm = (e) => {
    e.preventDefault();
    setisError(false);
    resetErrs();

    validateEmail();
    validatePass();

    if (!isError) {
      props.authLogin(loginCreds);
    }
  };

  return (
    <Form className="login-form" onSubmit={validateForm}>
      <Form.Field>
        <Form.Input
          id="form-input-control-error-email"
          fluid
          icon="envelope open outline"
          iconPosition="left"
          placeholder="Your email address"
          value={loginCreds.email}
          error={emailErr}
          onChange={(event) => {
            const _email = event.target.value;
            setLoginCreds((prev) => ({
              ...prev,
              email: _email,
            }));
            validateEmail();
          }}
        />
      </Form.Field>
      <Form.Field>
        <Form.Input
          fluid
          type="password"
          id="form-input-control-error-password"
          icon="eye slash outline"
          iconPosition="left"
          placeholder="Password"
          value={loginCreds.password}
          error={passErr}
          onChange={(event) => {
            const _pass = event.target.value;
            setLoginCreds((prev) => ({
              ...prev,
              password: _pass,
            }));
            validatePass();
          }}
        />
      </Form.Field>
      <Form.Field>
        <Form.Button
          id="form-button-control-public"
          fluid
          iconPosition="left"
          color="blue"
          onClick={validateForm}
          loading={props.isLoading}
          disabled={isError}
        >
          LOGIN
        </Form.Button>
      </Form.Field>
    </Form>
  );
});

const mapStateToProps = (state) => {
  return {
    user: state.user,
    isAuthenticated: state.isAuthenticated,
    isLoading: state.isLoading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    authLogin: (loginCreds) => dispatch(authLogin(loginCreds)),
  };
};

export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(LoginForm)
);
