import appRoutes from "./routes";

const blockedPaths = [
  appRoutes.onboardingProfilePage,
  appRoutes.onboardingPage,
  appRoutes.chromeExtensionPage,
];

const pages = {
  "1": (path) => appRoutes.onboardingProfilePage,
  "2": (path) => appRoutes.onboardingPage,
  "3": (path) => {
    return appRoutes.chromeExtensionPage;
    // if (localStorage.getItem("downloadChromeExtension"))
    // return appRoutes.homePage;
  },
  "4": (path) => {
    console.log(
      path,
      blockedPaths.find((data) => {
        console.log(data, path, data === path);
        return data === path;
      })
    );
    if (blockedPaths.find((data) => data === path)) return appRoutes.homePage;
    else return false;
  },
};

const switchPage = (onboardingPhase = 4, path) => {
  return pages[onboardingPhase](path);
};

export default switchPage;
