import React, { useEffect } from "react";
import DefaultLayout from "../../components/layouts/Default/Default";
import { Grid, Image } from "semantic-ui-react";
import "./Login.scss";
import AuthCard from "../../components/cards/AuthCard/AuthCard";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import appRoutes from "../../configs/routes";

const LoginPage = (props) => {
  useEffect(() => {
    // if (props.isAuthenticated) props.history.push(appRoutes.homePage);
  }, [props.isAuthenticated]);

  return (
    <div className="login-banner">
      <DefaultLayout>
        <Grid
          columns="1"
          className="login"
          style={{
            paddingTop: "10rem",
          }}
        >
          <Grid.Row>
            <Grid.Column mobile={16} tablet={12} computer={5}>
              <AuthCard></AuthCard>
            </Grid.Column>
          </Grid.Row>
        </Grid>
      </DefaultLayout>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    userApps: state.userApps,
    isAuthenticated: state.isAuthenticated,
    allApps: state.allApps,
    isLoading: state.isLoading,
  };
};

export default withRouter(connect(mapStateToProps, null)(LoginPage));
