import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import appRoutes from "../../../configs/routes";
import { updateUserProfile } from "../../../store/actions";
import { Form, Grid, Dropdown, Header } from "semantic-ui-react";
import "./UserProfileForm.scss";

const UserProfileForm = React.memo((props) => {
  const [organizationErr, setorganizationErr] = useState(null);
  const [nameErr, setnameErr] = useState(null);
  const [emailErr, setemailErr] = useState(null);
  const [isError, setisError] = useState(true);
  const [userProfile, setuserProfile] = useState({
    name: props.user.name,
    email: props.user.email,
    organization: props.user.organization.name,
  });

  useEffect(() => {
    console.log(isError);
  }, [isError]);

  useEffect(() => {
    validateName();
    validateOrganization();
    validateEmail();
  }, [userProfile]);

  const validateOrganization = (show = false) => {
    setorganizationErr(false);
    setisError(false);

    if (!userProfile.organization) {
      show &&
        setorganizationErr({
          content: "Enter App Url",
          pointing: "below",
        });
      setisError(true);
    }
  };

  const validateName = (show = false) => {
    setnameErr(false);
    setisError(false);

    if (!userProfile.name) {
      show &&
        setnameErr({
          content: "Enter App Name",
          pointing: "below",
        });
      setisError(true);
    }
  };

  const validateEmail = () => {
    setemailErr(null);
    setisError(false);
    const emailRegex = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;

    if (!userProfile.email) {
      setemailErr({
        content: "Enter your email address",
        pointing: "below",
      });
      setisError(true);
    } else if (!emailRegex.test(userProfile.email)) {
      setemailErr({
        content: "Invalid email address",
        pointing: "below",
      });
      setisError(true);
    }
  };

  const validateForm = (e) => {
    e.preventDefault();

    validateEmail(true);
    validateName(true);
    validateOrganization(true);

    if (!isError) {
      props.updateUserProfile(userProfile);
      setTimeout(() => {
        props.saved(true);
      }, 1500);
    }
  };

  return (
    <Form onSubmit={validateForm}>
      <Form.Field
        style={{
          margin: "2rem 0",
        }}
      >
        <Form.Input
          id="form-input-control-error-name"
          fluid
          icon="user outline"
          iconPosition="left"
          placeholder="Your full name"
          value={userProfile.name}
          error={nameErr}
          onChange={(event) => {
            const _name = event.target.value;
            setuserProfile((prev) => ({
              ...prev,
              name: _name,
            }));
            validateName();
          }}
        />
      </Form.Field>
      <Form.Field
        style={{
          margin: "2rem 0",
        }}
      >
        <Form.Input
          id="form-input-control-error-organization"
          fluid
          icon="building outline"
          iconPosition="left"
          placeholder="Company name"
          value={userProfile.organization}
          error={organizationErr}
          onChange={(event) => {
            const _organization = event.target.value;
            setuserProfile((prev) => ({
              ...prev,
              organization: _organization,
            }));
            validateOrganization();
          }}
        />
      </Form.Field>
      <Form.Field
        style={{
          margin: "2rem 0",
        }}
      >
        <Form.Input
          id="form-input-control-error-email"
          fluid
          icon="envelope open outline"
          iconPosition="left"
          placeholder="Email address"
          value={userProfile.email}
          error={emailErr}
          onChange={(event) => {
            const _email = event.target.value;
            setuserProfile((prev) => ({
              ...prev,
              email: _email,
            }));
            validateEmail();
          }}
        />
      </Form.Field>
      <Form.Field>
        <Form.Button
          id="form-button-control-public"
          fluid
          iconPosition="left"
          className="padded"
          color="green"
          onClick={validateForm}
          loading={props.isLoading}
          disabled={isError}
        >
          Save Changes
        </Form.Button>
      </Form.Field>
    </Form>
  );
});

const mapStateToProps = (state) => {
  return {
    userApps: state.userApps,
    isLoading: state.isLoading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    updateUserProfile: (userProfile) =>
      dispatch(updateUserProfile(userProfile)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(UserProfileForm);
