import React, { useState } from "react";
import { connect } from "react-redux";
import {
  Grid,
  Header,
  Loader,
  Card,
  Image,
  Button,
  Form,
} from "semantic-ui-react";
import HomeLayout from "../../components/layouts/Home/Home";
import user_image from "../../assets/icons/user_image.svg";
import UserProfileForm from "../../components/forms/UserProfileForm/UserProfileForm";
import UpdatePasswordForm from "../../components/forms/UpdatePasswordForm/UpdatePasswordForm";
import { updateUserProfile } from "../../store/actions";
import "./AccountSettings.scss";

const AccountSettings = (props) => {
  const [toggleProfile, setToggleProfile] = useState(false);

  const onProfileClick = () => {
    setToggleProfile(true);
  };

  return (
    <HomeLayout>
      <Grid container columns="3">
        <Grid.Row centered>
          <Grid.Column mobile={16} tablet={16} computer={10}>
            <Header>
              <div className="profile-title">Profile Settings</div>
            </Header>
          </Grid.Column>
        </Grid.Row>

        <Grid.Row stretched centered>
          <Grid.Column mobile={16} tablet={16} computer={10}>
            <Card fluid>
              <Card.Content
                className="profile-content"
                onClick={onProfileClick}
              >
                Edit Profile
              </Card.Content>
              <Grid container className="profile-settings">
                <Grid.Row>
                  <Grid.Column width={6} className="profile-image">
                    <Image src={user_image} size="small" circular centered />
                  </Grid.Column>
                  <Grid.Column width={10} className="profile-details">
                    {toggleProfile && (
                      <UserProfileForm
                        user={props.user}
                        saved={() => {
                          setToggleProfile(false);
                        }}
                      />
                    )}
                    {!toggleProfile && (
                      <div>
                        <Header size="large" className="profile-name">
                          {props.user && props.user.name}
                        </Header>
                        <Header size="small" className="profile-company">
                          {props.user && props.user.organization.name}
                        </Header>
                        <Header size="small" className="profile-company">
                          {props.user && props.user.email}
                        </Header>
                      </div>
                    )}
                  </Grid.Column>
                </Grid.Row>
              </Grid>
            </Card>
          </Grid.Column>
        </Grid.Row>
        <Grid.Row centered>
          <Grid.Column mobile={16} tablet={16} computer={10}>
            <Header>
              <div className="profile-title">Account Settings</div>
            </Header>
          </Grid.Column>
        </Grid.Row>
        <Grid.Row stretched centered>
          <Grid.Column mobile={16} tablet={16} computer={10}>
            <Card fluid>
              <div className="profile-settings-account">
                <Grid container>
                  <Grid.Row>
                    <Grid.Column width={16}>
                      <UpdatePasswordForm />
                    </Grid.Column>
                  </Grid.Row>
                </Grid>
              </div>
            </Card>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    </HomeLayout>
  );
};

const mapStateToProps = (state) => {
  return {
    user: state.user,
    allApps: state.allApps,
    isLoading: state.isLoading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    addUserApps: (payload) =>
      dispatch(
        updateUserProfile({
          userApps: payload,
        })
      ),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(AccountSettings);
