import React, { useState, useEffect } from "react";
import "./AppCard.scss";
import { Grid, Image, Card, Icon, Modal } from "semantic-ui-react";
import EditAppUrlForm from "../../forms/EditAppUrlForm/EditAppUrlForm";
import { updateFrequentApps } from "../../../store/actions";
import { connect } from "react-redux";

const AppCard = React.memo((props) => {
  const [isSelected, setisSelected] = useState(false);
  const [openEditModal, setopenEditModal] = useState(false);

  const { alreadySelected } = props;

  useEffect(() => {
    if (alreadySelected) setisSelected(alreadySelected);
  }, [alreadySelected]);

  useEffect(() => {
    setisSelected(props.isSelected);
  }, [props.isSelected]);

  useEffect(() => {
    props.onClicked(isSelected, {
      app: props.data._id,
      url: props.data.url,
    });
  }, [isSelected]);

  const onCardClick = () => {
    setisSelected((prev) => !prev);
  };

  const hitURL = () => {
    console.log("here");
    props.updateFrequentApps(props.data.appId);
    setTimeout(() => {
      window.location.href = props.data.url;
    }, 300);
  };

  return (
    <React.Fragment>
      {!props.clickable && (
        <Icon
          name="chain"
          className="AppCard-edit"
          title={`Edit ${props.data.name} URL`}
          onClick={() => setopenEditModal(true)}
        />
      )}
      <Card
        className={isSelected ? "AppCard active" : "AppCard"}
        onClick={props.clickable ? onCardClick : hitURL}
        // as="a"
        title={`Click to launch ${props.data.name}`}
        // href={!props.clickable && hitURL}
      >
        <Card.Content className="AppCard-banner">
          <Image src={props.data.icon} className="AppCard-image"></Image>
        </Card.Content>
        <Card.Content className="AppCard-content">
          <Card.Header>
            <div className="AppCard-content-title">{props.data.name}</div>
          </Card.Header>
        </Card.Content>
      </Card>
      <Modal
        open={openEditModal}
        size="mini"
        closeIcon
        onClose={() => {
          setopenEditModal(false);
        }}
      >
        <Modal.Content>
          <EditAppUrlForm
            app={props.data}
            submitted={() => {
              setopenEditModal(false);
            }}
          />
        </Modal.Content>
      </Modal>
    </React.Fragment>
  );
});

const mapDispatchToProps = (dispatch) => {
  return {
    updateFrequentApps: (payload) =>
      dispatch(
        updateFrequentApps({
          app: payload,
        })
      ),
  };
};

export default connect(null, mapDispatchToProps)(AppCard);
