import React, { useEffect } from "react";
import HomeLayout from "../../components/layouts/Home/Home";
import "./Home.scss";
import AppCard from "../../components/cards/AppCard/AppCard";
import AppCardSkeleton from "../../components/skeletons/AppCardSkeleton/AppCardSkeleton";
import {
  getAllApps,
  getUserDetails,
  addUserApps,
} from "../../store/actions/index";
import { connect } from "react-redux";

import { Grid, Header, Icon, Button } from "semantic-ui-react";

import { Result } from "antd";

const HomePage = (props) => {
  useEffect(() => {
    if (localStorage.getItem("initialAppLoad")) {
      console.log("initialLoad", localStorage.getItem("initialAppLoad"));
      const getHref = localStorage.getItem("initialAppLoad");
      localStorage.removeItem("initialAppLoad");
      window.location.href = getHref;
    }
    // window.location.href = "https://mail.google.com";
    props.getAllApps();
    props.getUserDetails();
  }, []);

  return (
    <HomeLayout>
      <Grid container columns="4">
        {props.isLoading || props.frequentApps.length > 0 ? (
          <React.Fragment>
            <Grid.Row centered>
              <Grid.Column mobile={16} tablet={16} computer={10}>
                <Header>
                  <div className="Home-title">Frequent Apps</div>
                </Header>
              </Grid.Column>
            </Grid.Row>
            <Grid.Row stretched centered>
              <Grid.Column mobile={16} tablet={16} computer={10}>
                <Grid columns="4">
                  <Grid.Row stretched>
                    {props.isLoading &&
                      [1, 2, 3, 4, 5, 6].map((obj) => (
                        <Grid.Column
                          mobile={6}
                          tablet={4}
                          computer={4}
                          style={{
                            margin: "2rem 0",
                          }}
                        >
                          <AppCardSkeleton />
                        </Grid.Column>
                      ))}
                    {props.frequentApps.map((obj) => (
                      <Grid.Column
                        mobile={6}
                        tablet={4}
                        computer={4}
                        style={{
                          margin: "2rem 0",
                        }}
                      >
                        <AppCard
                          data={obj}
                          clickable={false}
                          onClicked={() => {}}
                        ></AppCard>
                      </Grid.Column>
                    ))}
                  </Grid.Row>
                </Grid>
              </Grid.Column>
            </Grid.Row>
          </React.Fragment>
        ) : (
          ""
        )}
        <Grid.Row centered>
          <Grid.Column mobile={16} tablet={16} computer={10}>
            <Header>
              <div className="Home-title">All User Apps</div>
            </Header>
          </Grid.Column>
        </Grid.Row>
        <Grid.Row stretched centered>
          <Grid.Column mobile={16} tablet={16} computer={10}>
            <Grid columns="4">
              <Grid.Row stretched>
                {props.isLoading &&
                  [1, 2, 3, 4, 5, 6].map((obj) => (
                    <Grid.Column
                      mobile={6}
                      tablet={4}
                      computer={4}
                      style={{
                        margin: "2rem 0",
                      }}
                    >
                      <AppCardSkeleton />
                    </Grid.Column>
                  ))}
                {props.userApps.map((obj) => (
                  <Grid.Column
                    mobile={6}
                    tablet={4}
                    computer={4}
                    style={{
                      margin: "2rem 0",
                    }}
                  >
                    <AppCard
                      data={obj}
                      clickable={false}
                      onClicked={() => {}}
                    ></AppCard>
                  </Grid.Column>
                ))}
                {!props.isLoading && !props.userApps.length && (
                  <Grid.Column mobile={16} tablet={16} computer={16}>
                    <Result
                      status="warning"
                      title="No Apps Found."
                      subTitle="To get application you need to App your application. Click below to add."
                      extra={
                        <Button color="primary" circular basic>
                          <Icon name="add" /> ADD APPS
                        </Button>
                      }
                    />
                  </Grid.Column>
                )}
              </Grid.Row>
            </Grid>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    </HomeLayout>
  );
};

const mapStateToProps = (state) => {
  return {
    userApps: state.userApps,
    frequentApps: state.frequentApps
      .sort((a, b) => {
        if (a.frequency > b.frequency) return -1;
        else if (a.frequency < b.frequency) return 1;
        else return 0;
      })
      .slice(0, 5),
    error: state.err,
    isLoading: state.isLoading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    getAllApps: () => dispatch(getAllApps()),
    getUserDetails: () => dispatch(getUserDetails()),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(HomePage);
