// Auth Login User Actions
export const retryLogin = (payload = "") => {
  return (dispatch) => {
    dispatch({
      type: actionTypes.RETRY_LOGIN,
      payload: payload,
    });
    if (localStorage.getItem("X-auth-token")) {
      dispatch(getUserDetails());
      dispatch(getAllApps());
      dispatch(getAllAppCategories());
      dispatch(retryLoginSuccess(localStorage.getItem("X-auth-token")));
    } else dispatch(retryLoginFailure("No User Authentication Found"));
  };
};

export const retryLoginSuccess = (payload) => {
  return {
    type: actionTypes.RETRY_LOGIN_SUCCESS,
    payload: payload,
  };
};

export const retryLoginFailure = (payload) => {
  return {
    type: actionTypes.RETRY_LOGIN_FAILURE,
    payload: payload,
  };
};

export const logout = (payload) => {
  return (dispatch) => {
    dispatch({
      type: actionTypes.LOGOUT,
      payload: payload,
    });
    try {
      localStorage.removeItem("X-auth-token");
      localStorage.removeItem("downloadChromeExtension");
      localStorage.removeItem("refreshPage");
      if (!localStorage.getItem("X-auth-token"))
        dispatch(logoutSuccess("Logout"));
      else dispatch(logoutFailure("Logout Failed"));
    } catch (e) {
      dispatch(logoutFailure("Logout Failed"));
    }
  };
};

export const logoutSuccess = (payload) => {
  return {
    type: actionTypes.LOGOUT_SUCCESS,
    payload: payload,
  };
};

export const logoutFailure = (payload) => {
  return {
    type: actionTypes.LOGOUT_FAILURE,
    payload: payload,
  };
};

export const authLogin = (payload) => {
  return (dispatch) => {
    dispatch({
      type: actionTypes.AUTH_LOGIN,
      payload: payload,
    });
    axiosInstance
      .post("/auth/", payload)
      .then((resp) => {
        dispatch(getUserDetails());
        dispatch(authLoginSuccess(resp.data));
      })
      .catch((err) => dispatch(authLoginFailure(err)));
  };
};

export const authLoginSuccess = (payload) => {
  localStorage.setItem("X-auth-token", payload.token);
  return {
    type: actionTypes.AUTH_LOGIN_SUCCESS,
    payload: payload,
  };
};

export const authLoginFailure = (payload) => {
  return {
    type: actionTypes.AUTH_LOGIN_FAILURE,
    payload: payload,
  };
};

