// Update User Profile Actions
export const updateUserProfile = (payload, isOnboarding = false) => {
  return (dispatch) => {
    dispatch({
      type: actionTypes.UPDATE_USER_DETAILS,
      payload: payload,
    });
    axiosInstance
      .post("/profile/", payload)
      .then((resp) => {
        if (isOnboarding && payload.userApps && payload.userApps[0].url) {
          dispatch(
            updateUserProfileSuccess(resp.data, payload.userApps[0].url)
          );
        } else {
          dispatch(updateUserProfileSuccess(resp.data));
        }
      })
      .catch((err) => dispatch(updateUserProfileFailure(err)));
  };
};

export const updateUserProfileSuccess = (payload, firstLoad = null) => {
  if (firstLoad) {
    localStorage.setItem("initialAppLoad", firstLoad);
  }
  message.success("Profile Updated");
  return {
    type: actionTypes.UPDATE_USER_DETAILS_SUCCESS,
    payload: payload,
  };
};

export const updateUserProfileFailure = (payload) => {
  return {
    type: actionTypes.UPDATE_USER_DETAILS_FAILURE,
    payload: payload,
  };
};

