import "../../../styles/index.scss";
import React, { useState, useEffect, useRef, createRef } from "react";
import { Link } from "react-router-dom";
import {
  Container,
  Sticky,
  Menu,
  Button,
  Icon,
  Grid,
  Image,
  Dropdown,
  Header,
} from "semantic-ui-react";
import "./Home.scss";
import headerUserLogo from "../../../assets/icons/header-user.svg";
import settingsLogo from "../../../assets/icons/settings.svg";
import logoutLogo from "../../../assets/icons/logout.svg";
import environments from "../../../configs/environments/environments";
import { connect } from "react-redux";
import { logout } from "../../../store/actions";
import { withRouter } from "react-router-dom";
import appRoutes from "../../../configs/routes";

const HomeLayout = React.memo((props) => {
  const userDropdowns = [
    {
      key: "settings-link",
      icon: settingsLogo,
      text: "Settings",
      subheader: "Control your account settings",
      value: "Settings",
      logic: () => {
        props.history.push(appRoutes.accountSettingsPage);
      },
    },
    {
      key: "logout-link",
      icon: logoutLogo,
      text: "Logout",
      subheader: "",
      value: "Logout",
      logic: () => {
        props.logout();
      },
    },
  ];

  const [rightSidebarSize, setRightSidebarSize] = useState(0);
  const [reducedHeight, setReducedHeight] = useState(100);

  return (
    <React.Fragment>
      <div
        className="mainContainer"
        style={{
          marginLeft: rightSidebarSize - 6,
          height: reducedHeight + "%",
          overflowY: "auto",
        }}
      >
        {/* Main Content Starts */}
        <Sticky>
          <Container fluid className="home">
            <Container>
              <Grid.Row className="home-header">
                <Menu
                  icon="labeled"
                  size="small"
                  position="right"
                  className="home-header-menu"
                >
                  <Link
                    as="div"
                    className="home-header-logo"
                    to={appRoutes.homePage}
                  >
                    {environments.headerName}
                  </Link>
                  <Menu.Menu position="right" size="small">
                    <Link to={appRoutes.addAppsPage}>
                      <Button className="home-header-button" basic circular>
                        <Icon name="add " />
                        ADD APPS
                      </Button>
                    </Link>
                    <Dropdown
                      pointing
                      item
                      link
                      className="home-header-user-dropdown"
                      text={
                        <Image
                          src={headerUserLogo}
                          size="massive"
                          circular
                          style={{
                            height: "4rem",
                          }}
                        />
                      }
                      options={userDropdowns}
                    >
                      <Dropdown.Menu>
                        {userDropdowns.map((obj) => (
                          <Dropdown.Item onClick={obj.logic}>
                            <Header
                              as="h5"
                              icon={<Image src={obj.icon} />}
                              content={obj.text}
                              subheader={obj.subheader}
                            />
                          </Dropdown.Item>
                        ))}
                      </Dropdown.Menu>
                    </Dropdown>
                  </Menu.Menu>
                </Menu>
              </Grid.Row>
            </Container>
          </Container>
        </Sticky>

        <Container fluid className="main-content">
          {props.children}
        </Container>
      </div>
    </React.Fragment>
  );
});

const mapStateToProps = (state) => {
  return {
    isAuthenticated: state.isAuthenticated,
    error: state.err,
    isLoading: state.isLoading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    logout: () => dispatch(logout()),
  };
};

export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(HomeLayout)
);
