import React, { useState, useEffect, useRef } from "react";
import HomeLayout from "../../components/layouts/Home/Home";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { getAllApps, updateUserApps } from "../../store/actions/index";
import { Grid, Modal, Header, Button, Loader, Icon } from "semantic-ui-react";
import userImageIcon from "../../assets/icons/user_image.svg";
import appRoutes from "../../configs/routes";
import AppCard from "../../components/cards/AppCard/AppCard";
import "./AddApps.scss";
import AddAppForm from "../../components/forms/AddAppForm/AddAppForm";

const AddAppsPage = (props) => {
  const [selectedApps, setselectedApps] = useState([]);
  const [isAddAppModal, setisAddAppModal] = useState(false);
  const [isUpdateAPICalled, setisUpdateAPICalled] = useState(false);

  const [customAllApps, setcustomAllApps] = useState([]);

  useEffect(() => {
    props.getAllApps();
    if (props.isAuthenticated && props.user && props.user.onboardingPhase < 2)
      props.history.push(appRoutes.loginPage);
  }, []);

  useEffect(() => {
    if (!props.isLoading && isUpdateAPICalled) {
      setisUpdateAPICalled(false);
      props.history.push(appRoutes.homePage);
    }
  }, [props.isLoading, isUpdateAPICalled]);

  useEffect(() => {
    if (props.userApps && props.allApps) {
      setcustomAllApps(
        props.allApps.map((o1) => {
          const app = props.userApps.find((o2) => o2.appId === o1._id);
          return {
            ...o1,
            active: app ? true : false,
          };
        })
      );
    }
  }, [props.userApps, props.allApps]);

  const onAppsSelects = (isSelected, obj) => {
    if (!isSelected)
      setselectedApps((prev) => {
        return prev.filter((ele) => ele.app !== obj.app);
      });
    else setselectedApps((prev) => prev.concat(obj));
  };

  const onSubmit = () => {
    props.addUserApps(selectedApps);
    setisUpdateAPICalled(true);
  };

  return (
    <HomeLayout>
      <Grid container columns="3" className="login">
        <Grid.Row centered className="AddApps">
          <Grid.Column
            mobile={16}
            tablet={16}
            computer={10}
            className="AddApps-header"
          >
            <Button
              basic
              color="red"
              size="large"
              onClick={() => {
                setisAddAppModal(true);
              }}
            >
              <Icon name="add circle" />
              Add New App
            </Button>
            <Modal
              open={isAddAppModal}
              size="tiny"
              closeIcon
              closeOnEscape={true}
              closeOnDimmerClick={false}
              onClose={() => setisAddAppModal(false)}
            >
              <Modal.Content>
                <AddAppForm close={() => setisAddAppModal(false)} />
              </Modal.Content>
            </Modal>
            <Button
              floated
              color="green"
              size="large"
              disabled={!selectedApps.length}
              onClick={onSubmit}
              loading={props.isLoading}
            >
              Select Apps
            </Button>
          </Grid.Column>
          <Grid.Column
            mobile={16}
            tablet={16}
            computer={10}
            className="AddApps-heading"
          >
            <Header
              as="h2"
              style={{
                margin: "2rem 0",
              }}
              subheader="Select the apps that power your business"
            />
          </Grid.Column>
        </Grid.Row>
        <Grid.Row stretched centered>
          <Grid.Column mobile={16} tablet={16} computer={10}>
            <Grid columns="5">
              <Grid.Row
                style={{
                  minHeight: 400,
                }}
              >
                <Loader size="large" active={!customAllApps.length}>
                  Loading
                </Loader>
                {customAllApps.map((obj) =>
                  obj.header ? (
                    <Grid.Column
                      mobile={16}
                      tablet={16}
                      computer={16}
                      style={{
                        margin: "1rem 0",
                      }}
                    >
                      <div className="AddApps-title">{obj.header}</div>
                    </Grid.Column>
                  ) : (
                    <Grid.Column
                      mobile={6}
                      tablet={4}
                      computer={4}
                      style={{
                        margin: "2rem 0",
                      }}
                    >
                      <AppCard
                        data={obj}
                        onClicked={onAppsSelects}
                        clickable={true}
                        isSelected={obj.active}
                      ></AppCard>
                    </Grid.Column>
                  )
                )}
                <Grid.Column
                  mobile={16}
                  tablet={16}
                  computer={16}
                  style={{
                    padding: "2rem 0",
                  }}
                >
                  <Button
                    fluid
                    color="green"
                    size="large"
                    disabled={!selectedApps.length}
                    onClick={onSubmit}
                    loading={props.isLoading}
                  >
                    Select Apps
                  </Button>
                </Grid.Column>
              </Grid.Row>
            </Grid>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    </HomeLayout>
  );
};

const mapStateToProps = (state) => {
  return {
    userApps: state.userApps,
    user: state.user,
    allApps: state.allApps,
    isLoading: state.isLoading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    getAllApps: () => dispatch(getAllApps()),
    addUserApps: (payload) =>
      dispatch(
        updateUserApps({
          userApps: payload,
        })
      ),
  };
};

export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(AddAppsPage)
);
