import React, { useEffect } from "react";
import { Button } from "semantic-ui-react";
import environments from "../../../configs/environments/environments";

const VerifyChromeExtension = (props) => {
  if (localStorage.getItem("refreshPage")) {
    localStorage.removeItem("refreshPage");
    props.verification(true);
  }

  const verifyInstalltion = () => {
    if (!localStorage.getItem("refreshPage")) {
      localStorage.setItem("refreshPage", true);
      window.location.reload(false);
      return;
    }

    const element = document.getElementById(
      "dashdashgo-chrome-extension-verification"
    );
    if (!element) {
      props.verification(false);
      localStorage.removeItem("refreshPage");
    } else {
      props.verification(true);
    }
  };
  return (
    <React.Fragment>
      <div
        style={{
          marginBottom: "1rem",
        }}
      >
        Missing with chrome extension download link?{" "}
        <a
          href={environments.chromeExtensionLink}
          style={{
            color: "blue",
          }}
        >
          Click here to download.
        </a>
      </div>
      <Button
        circular
        color="orange"
        size="large"
        style={{ ...props.style }}
        onClick={verifyInstalltion}
        isLoading={props.isLoading}
      >
        Verify Chrome Installation
      </Button>
    </React.Fragment>
  );
};

export default VerifyChromeExtension;
