import React, { useState } from "react";
import LoginForm from "../../forms/LoginForm/LoginForm";
import SignupForm from "../../forms/SignupForm/SignupForm";
import { Button, Card, Divider, Transition } from "semantic-ui-react";
import "./AuthCard.scss";

const AuthCard = React.memo((props) => {
  const [isLogin, setIsLogin] = useState(true);
  const [isSignup, setIsSignup] = useState(false);
  const [isSecondTitle, setisSecondTitle] = useState(false);

  const resetAuthSelects = () => {
    setIsLogin(false);
    setIsSignup(false);
  };
  const showSecondTitle = () => {
    setisSecondTitle(true);
  };
  const completeSignup = () => {};

  return (
    <React.Fragment>
      <div className="auth">
        <Transition.Group animation="horizontal flip" duration="300">
          {isSignup && (
            <Card fluid className="loginCard">
              <Card.Content>
                <h1 className="loginCard-headline">
                  {!isSecondTitle && "Register with us"}
                  {isSecondTitle && "Complete your profile and Let's begin."}
                </h1>
                <SignupForm showSecondStep={showSecondTitle}></SignupForm>

                <div style={{ marginTop: "3rem" }}>
                  Already have account?
                  <span
                    style={{ color: "blue", cursor: "pointer" }}
                    onClick={() => {
                      setIsLogin(true);
                      setIsSignup(false);
                    }}
                  >
                    {" "}
                    Click Here
                  </span>
                </div>
              </Card.Content>
            </Card>
          )}
          {isLogin && (
            <Card fluid className="loginCard">
              <Card.Content>
                <h1 className="loginCard-headline">
                  Welcome! Please Login to your account
                </h1>
                <LoginForm></LoginForm>
                <div style={{ marginTop: "3rem" }}>
                  New to RockStack Capital?
                  <span
                    style={{ color: "blue", cursor: "pointer" }}
                    onClick={() => {
                      setIsLogin(false);
                      setIsSignup(true);
                    }}
                  >
                    {" "}
                    Click Here Register
                  </span>
                </div>
              </Card.Content>
            </Card>
          )}
        </Transition.Group>
      </div>
    </React.Fragment>
  );
});

export default AuthCard;
