import React, { useState, useEffect } from "react";
import { Form, Transition, Button } from "semantic-ui-react";
import { withRouter } from "react-router-dom";
import { authRegister, updateUserProfile } from "../../../store/actions";
import { connect } from "react-redux";
import "./ProfileForm.scss";
import appRoutes from "../../../configs/routes";

const ProfileForm = React.memo((props) => {
  const [nameErr, setNameErr] = useState(null);
  const [companyErr, setCompanyErr] = useState(null);
  const [companySizeErr, setCompanySizeErr] = useState(null);
  const [signupCreds, setSignupCreds] = useState({
    name: "",
    company: "",
    companySize: "",
  });

  const [isError, setisError] = useState(true);

  useEffect(() => {
    validateName();
    validateCompany();
    validateCompanySize();
  }, [signupCreds]);

  // useEffect(() => {
  //   if (props.user && props.user.organization) {
  //     setSignupCreds((prev) => {
  //       return {
  //         ...prev,
  //         company: props.user.organization,
  //       };
  //     });
  //   }
  //   if (props.user && props.user.name) {
  //     setSignupCreds((prev) => {
  //       return {
  //         ...prev,
  //         name: props.user.name,
  //       };
  //     });
  //   }
  // }, [props.user]);

  const resetErrs = () => {
    setNameErr(null);
    setCompanyErr(null);
    setisError(false);
  };

  const validateName = (show = false) => {
    setNameErr(null);
    setisError(false);
    if (!signupCreds.name) {
      show &&
        setNameErr({
          content: "Enter your full name",
          pointing: "below",
        });
      setisError(true);
    }
  };

  const validateCompany = (show = false) => {
    setCompanySizeErr(null);
    setisError(false);
    if (!signupCreds.company) {
      show &&
        setCompanySizeErr({
          content: "Enter your company name",
          pointing: "below",
        });
      setisError(true);
    }
  };

  const validateCompanySize = (show = false) => {
    setCompanyErr(null);
    setisError(false);
    if (!signupCreds.companySize) {
      show &&
        setCompanyErr({
          content: "Enter your company size",
          pointing: "below",
        });
      setisError(true);
    }
  };

  const validateForm = (e) => {
    e.preventDefault();
    resetErrs();
    validateName(true);
    validateCompany(true);
    validateCompanySize(true);

    if (!isError) {
      props.submitted(signupCreds);
    }
  };

  return (
    <Transition.Group animation="horizontal flip" duration="300">
      <Form className="signup-form" onSubmit={validateForm}>
        <React.Fragment>
          <Form.Field>
            <Form.Input
              fluid
              icon="user outline"
              iconPosition="left"
              placeholder="Full name"
              value={signupCreds.name}
              error={nameErr}
              style={{
                margin: "2rem 0",
              }}
              onChange={(event) => {
                const _name = event.target.value;
                setSignupCreds((prev) => ({
                  ...prev,
                  name: _name,
                }));
                validateName();
              }}
            />
          </Form.Field>
          <Form.Field>
            <Form.Input
              fluid
              icon="building outline"
              iconPosition="left"
              placeholder="Company Name"
              value={signupCreds.company}
              error={companyErr}
              style={{
                margin: "2rem 0",
              }}
              onChange={(event) => {
                const _company = event.target.value;
                setSignupCreds((prev) => ({
                  ...prev,
                  company: _company,
                }));
                validateCompany();
              }}
            />
          </Form.Field>
          <Form.Field>
            <Form.Input
              fluid
              type="number"
              icon="group"
              iconPosition="left"
              placeholder="Company Size"
              value={signupCreds.companySize}
              error={nameErr}
              style={{
                margin: "2rem 0",
              }}
              onChange={(event) => {
                const _companySize = event.target.value;
                setSignupCreds((prev) => ({
                  ...prev,
                  companySize: _companySize,
                }));
                validateCompanySize();
              }}
            />
          </Form.Field>
          <Form.Field>
            <Form.Button
              fluid
              iconPosition="left"
              color="green"
              loading={props.isLoading}
              disabled={isError}
              onClick={validateForm}
            >
              Save Profile
            </Form.Button>
          </Form.Field>
        </React.Fragment>
      </Form>
    </Transition.Group>
  );
});

export default withRouter(ProfileForm);
