import React, { useEffect, useState, useConstructor } from "react";
import { Route, Switch, Redirect, withRouter } from "react-router-dom";
import HomePage from "./pages/Home/Home";
import LoginPage from "./pages/Login/Login";
import OnboardingPage from "./pages/Onboarding/Onboarding";
import "./App.css";
import "./styles/index.scss";
import appRoutes from "./configs/routes";
import "antd/dist/antd.css";
import { connect } from "react-redux";
import ChromeExtensionPage from "./pages/ChromeExtension/ChromeExtension";
import { retryLogin } from "./store/actions";
import OnboardingProfilePage from "./pages/OnboardingProfile/OnboardingProfile";
import AccountSettings from "./pages/AccountSettings/AccountSettings";
import switchPage from "./configs/onboardingHandler";
import AddAppsPage from "./pages/AddApps/AddApps";
import LogoutPage from "./pages/Logout";

const App = (props) => {
  const [routes, setroutes] = useState(null);

  useEffect(() => {
    props.retryLogin();
  }, [props.isAuthenticated]);

  useEffect(() => {
    if (props.user && props.user.onboardingPhase)
      props.history.push(
        switchPage(props.user.onboardingPhase, props.history.location.pathname)
      );
  }, [props.user]);

  useEffect(() => {
    if (props.isAuthenticated) {
      setroutes(
        <Switch>
          <Route
            path={appRoutes.chromeExtensionPage}
            exact
            component={ChromeExtensionPage}
          />
          <Route path={appRoutes.homePage} exact component={HomePage} />
          <Route
            path={appRoutes.onboardingPage}
            exact
            component={OnboardingPage}
          />
          <Route
            path={appRoutes.onboardingProfilePage}
            exact
            component={OnboardingProfilePage}
          />
          <Route path={appRoutes.addAppsPage} exact component={AddAppsPage} />
          <Route
            path={appRoutes.accountSettingsPage}
            exact
            component={AccountSettings}
          />
          <Route
            path={appRoutes.accountSettingsPage}
            exact
            component={AccountSettings}
          />
          <Route path={appRoutes.logoutPage} exact component={LogoutPage} />
          <Redirect path="*" to={appRoutes.homePage} />
        </Switch>
      );
    } else {
      setroutes(
        <Switch>
          <Route path={appRoutes.loginPage} exact component={LoginPage} />
          <Redirect path="*" to={appRoutes.loginPage} />
        </Switch>
      );
    }
  }, [props.isAuthenticated]);

  return <React.Fragment>{routes}</React.Fragment>;
};

const mapStateToProps = (state) => {
  console.log("APP STATE: ", state);
  return {
    user: state.user,
    isAuthenticated: localStorage.getItem("X-auth-token") ? true : false,
    isLoading: state.isLoading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    retryLogin: () => dispatch(retryLogin()),
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(App));
